.navcontainer{
    margin-right: 2px;
    width: 100%;
    background: rgb(27,3,3);
    background: -moz-linear-gradient(90deg, rgba(27,3,3,1) 0%, rgba(53,26,18,1) 21%, rgba(123,51,6,1) 61%);
    background: -webkit-linear-gradient(90deg, rgba(27,3,3,1) 0%, rgba(53,26,18,1) 21%, rgba(123,51,6,1) 61%);
    background: linear-gradient(90deg, rgba(27,3,3,1) 0%, rgba(53,26,18,1) 21%, rgba(123,51,6,1) 61%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#1b0303",endColorstr="#7b3306",GradientType=1); 
    min-height: 100px;
    display: flex;
    padding: 10px;
    justify-content: center;
    align-items: center;
    position: relative;
}
nav{
    width:100%;
}
#logo
{
    object-fit: cover;
    object-position: center;
    overflow: hidden;
    height: 80px;
    border-radius: 20px;
    box-shadow: 5px 2px 2px rgb(216, 209, 209);
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    height:100%;
    width:100%;
    margin:2px;
    border: 5px solid black ;
    border-radius: 5px;
    background: rgb(217,205,209);
    background: -moz-linear-gradient(90deg, rgba(217,205,209,1) 2%, rgba(255,255,255,1) 68%);
    background: -webkit-linear-gradient(90deg, rgba(217,205,209,1) 2%, rgba(255,255,255,1) 68%);
    background: linear-gradient(90deg, rgba(217,205,209,1) 2%, rgba(255,255,255,1) 68%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#d9cdd1",endColorstr="#ffffff",GradientType=1);
    overflow: scroll;

}
#navlist{
    list-style: none;
    font-size: 1.2rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.online{
    padding:25px;
    margin:2px;
    border-radius: 10px;
    background-color:chocolate;
    color:antiquewhite;
    border: none;
    transition:all 0.3s linear 0s;
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0); 
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
}
.online:hover{
    transform:scale(1.1);

}
.navitems>a{
    transition:all 0.3s linear 0s;
}
.navitems{
    display: flex;
    gap: 30px;
}
.navitems>a:hover{
    color:chocolate;
    transform:scale(1.4);
}
a{
    text-decoration: none;
    color: antiquewhite;
}
#navlogo{
    font-size: 2.5rem;
    color: antiquewhite;
    font-weight: bold;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.main{
    height: 500px;
    width: 1450px;
    border: 3px solid black;
    display: flex;
    overflow-x:auto ;
}
.footerbox{
    background: rgb(27,3,3);
    background: -moz-linear-gradient(90deg, rgba(27,3,3,1) 0%, rgba(53,26,18,1) 21%, rgba(123,51,6,1) 61%);
    background: -webkit-linear-gradient(90deg, rgba(27,3,3,1) 0%, rgba(53,26,18,1) 21%, rgba(123,51,6,1) 61%);
    background: linear-gradient(90deg, rgba(27,3,3,1) 0%, rgba(53,26,18,1) 21%, rgba(123,51,6,1) 61%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#1b0303",endColorstr="#7b3306",GradientType=1);
    text-align: center;
    min-height: 70px;
    padding: 40px;
    
}
.footerbox p{
    color: antiquewhite;
    font-size: 1rem;
}
.content{
    width: 100%;
    margin:auto;
    background-size: 100% 100%;
    background-position: 0px 0px,0px 0px,0px 0px,0px 0px,0px 0px;
    background-image: radial-gradient(49% 81% at 45% 47%, #554A0745 0%, #073AFF00 100%),radial-gradient(113% 91% at 17% -2%, #090500FF 1%, #FF000000 99%),radial-gradient(142% 91% at 83% 7%, #FF7D00FF 1%, #FF000000 99%),radial-gradient(142% 91% at -6% 74%, #FF7D00FF 1%, #FF000000 99%),radial-gradient(142% 91% at 111% 84%, #211C1C2B 0%, #FF0000FF 100%);
    object-fit:cover;
    overflow:hidden;
    display: flex;
    flex-direction: column;
    gap:15px;
    padding: 20px;
    box-shadow: 10px 5px 5px rgb(146, 53, 53);

}
#gulab{
    height:200px;
    width: 200px;
    border-radius: 20px;
    margin:20px;
    object-fit: cover;
    position: relative;
    transition:all 0.6s linear 0s;
}
.item{
    display: flex;
    gap:15px;
    justify-content: space-around;
}
.leftbox{
    display: flex;
    flex-direction: column;
    color: beige;
    gap:15px;
}
h1{

  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color:beige;
}
.ing{
    font-size: 15px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
     'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
     list-style: none;
}
.rightbox:hover{
    transform: scale(1.1);
}
.rightbox{
    position: relative;
    overflow: hidden;
}
.rightbox .overlay{
    position: absolute;
    top:0;
    left:0;
    width:200px;
    height:200px;
    background-color: rgb(28,26,26,0.513);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    margin:20px;
    opacity: 0;
    transition:all 0.3s linear 0s;

}
.rightbox:hover .overlay{
    opacity: 1;
}
span{
    color:antiquewhite;
    font-size: 15px;
}
.overview{
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    gap:15px;
    background-color: #FBAB7E;
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
    padding:20px;
    border-radius: 1rem;
    box-shadow: 10px 5px 5px rgb(239, 115, 57);
}
#ov{
    height:500px;
    width: 500px;
    object-fit:cover;
    border-radius: 1rem;
    box-shadow: 10px 5px 5px rgb(146, 53, 53);
}
#part1{
    color: #444444;
    text-shadow: 0 0 10px #FFFFFF;
    font-family:  "Palatino Linotype", "Book Antiqua", Palatino, serif;
    font-size: 40px;
    letter-spacing: 2px;
    word-spacing: 2px;
    font-weight: 700;
    text-decoration: none solid rgb(68, 68, 68);
    font-style: normal;
    font-variant: small-caps;
    text-transform: none;

}
.part2{
    color:rgb(129, 57, 57);
    margin-top:20px;
    font-size:large;
    font-weight: 600;
}
#part3{
    padding:25px;
    margin-top:30px;
    border-radius: 10px;
    background-color:#815854;
    color:antiquewhite;
    border: none;
    transition:all 0.3s linear 0s;
    font-size: large;
    box-shadow: 10px 5px 5px rgba(146, 53, 53, 0.722);
}
#part3:hover{
    transform:scale(1.1);

}
.divi{
    margin-top: 20px;
   background-color: black; 
   color:#FFFFFF;
   display: flex;
   padding:60px;
   justify-content: space-between;
   border-radius: 1rem;
   -webkit-box-shadow: 5px 5px 5px 0px #292626, inset 4px 4px 15px 0px #3a3030, 5px 5px 15px 5px rgba(87, 66, 66, 0); 
   box-shadow: 5px 5px 5px 0px #ded6d6, inset 4px 4px 15px 0px #dfdada, 5px 5px 15px 5px rgba(123, 113, 113, 0);
}
.divi1{
    padding-left: 20px;
    font-size: xx-large;
}
.divi2{
    font-size: medium;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    word-spacing: 2px;
}
.menu{
    background-color: #c95613;
    border-radius: 1rem;
    padding: 30px;
    -webkit-box-shadow: 5px 5px 15px 5px #000000; 
    box-shadow: 5px 5px 5px 0px #111111, inset 4px 4px 15px 0px #1e1d1d, 5px 5px 15px 5px rgba(123, 113, 113, 0);
}
#in{
    text-align: center;
    color:antiquewhite;
}
#out{
    text-align:center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    word-spacing: 2px;
    color:antiquewhite;
    margin-top:20px;
    margin-bottom: 20px;
    margin-bottom: 70px;
}
.A{
    display: flex;
    justify-content: space-between;
    padding: 50px 40px;
    border-radius: 20px;
}
.gap{
    list-style: none;
    margin-top: 15px;
    gap: 15px;
}
li{
    gap: 15px;
}
.ran{
    margin-bottom: 30px;
}
.search{
    padding:12px;
    border-radius:10px;
    background-color:brown;
    color:antiquewhite;
    border: none;
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0); 
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
    transition:all 0.3s linear 0s;
}
.search:hover{
    background-color: chocolate;
}
.search_bar{
    padding:12px;
    border-radius:10px;
    border: none; 
    background-color: rgb(169, 135, 135);
    color:#292626;
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0); 
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
}
.box{
    background-color: brown;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height:30%;
    width: 30%;
    padding:60px;
    -webkit-box-shadow: 5px 5px 5px 0px #000000, inset 4px 4px 15px 0px #000000, 5px 5px 15px 5px rgba(0,0,0,0); 
    box-shadow: 5px 5px 5px 0px #000000, inset 4px 4px 15px 0px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
    transition:all 0.3s linear 0s;
        
}
.box:hover{
    transform:scale(0.9);
}
.font{
    display:flex;
    justify-content: center;
    margin-bottom: 20px;

}
.box_head{
    display:flex;
    justify-content: center;
    margin-bottom: 20px;
}
.box_para{
    display:flex;
    justify-content: center;
    color:antiquewhite;
    font-size: large;
    word-spacing: 2px;
    text-align: center;
}
.font_out{
    background-color: white;
    border-radius: 50px;
}
.font_img{
    height: 80px;
    width: 80px;
    border-radius: 50%;
}
.sin{
    display:flex;
    justify-content: space-between;
}